<template>
  <div class="style">
    <top></top>
    <div>
      <div class="style_ms" v-if="show">
        <p>你目前暂无优惠券</p>
        <p>为你的客户提供优惠券能够帮你增加订单。</p>
        <p><img src="../../../assets/no-discount.png" style="width: 200px;height: 200px;"/></p>
        <p><a href="javascript:void(0)" @click="setCard">设置优惠券</a></p>
      </div>
        <!--<transition name="slide-fade">-->
          <setCard v-if="!show"></setCard>
        <!--</transition>-->
    </div>
  </div>
</template>
<script>
  import top from '../../top.vue'
  import setCard from './setCard.vue'
  export default{
    data(){
      return{
        show:true
      }
    },
    methods:{
      setCard(){
        this.show=!this.show;
      }
    },
    mounted(){
    },
    components:{
      top,
      setCard
    }
  }
</script>
<style scoped>
.style_ms{margin-top: 0.5rem}
p{text-align: center;padding: 0.1rem}
p{font-size: 0.15rem}
p:nth-child(1){
  font-size: 0.18rem;
  font-weight: 600;
}
  p>a{
    color: white;
    display: inline-block;
    padding: 0.11rem 0.3rem;
    border-radius: 0.05rem;
    background-color: #93b719;
  }
p>a:hover{
  opacity: 0.8;
}

p>img{
    margin:-0.2rem;
    display: inline-block;
    transform: scale(0.5);
  }
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>



