<template>
<div class="set">
  <div class="set_card">
    <h2>设置优惠券</h2>
    <ul style="display: flex">
      <li>
          <h3>优惠券</h3>
          <input type="text" v-model="name" style="width: 2.5rem" placeholder="例如:NEWYEAR26" @focus="textF"  @blur="textB"><br>
        <span style="color:#e64751;margin-top: 0.1rem;display: inline-block">{{msg}}</span>
      </li>
      <li style="margin-left: 0.15rem">
          <h3>截止日期</h3>
          <DatePicker type="date" :options="options" placeholder="选择日期" style="width: 200px;" :value="value" :disabled="disable"></DatePicker>
          <Checkbox v-model="single" class="box" @on-change="change">永远不失效</Checkbox>
      </li>
    </ul>
    <ul style="display: flex;margin-top: 0.2rem">
      <li>
        <h3>优惠类型</h3>
        <Select v-model="type_y" style="width:200px">
          <Option v-for="item in list" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </li>
      <li style="margin-left: 0.15rem;margin-top: 0.2rem" v-if="type_y!=='免运费'">
        <input type="text" v-model="per" style="width: 2.5rem" :placeholder="type_y" @focus="textF"  @blur="input"><span style="margin-left: -0.2rem">%</span><br>
      </li>
      <li  style="margin-left: 0.15rem;">
        <h3>适用条件</h3>
        <Select v-model="type_t" style="width:200px">
          <Option v-for="item in order" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </li>
    </ul>
    <span style="color:#e64751;margin-top: 0.1rem;display: inline-block" v-if="type_y!=='免运费'">{{perMsg}}</span><br>
    <span v-if="type_y==='免运费'">此优惠券将订单免运费</span>
    <span v-if="type_y==='固定金额'">此优惠券将使总金额减免 0.00 （运费除外）</span>
    <span v-if="type_y==='百分比'">此优惠券将使总金额减免 0% （运费除外）</span>
  </div>
  <footer>
    <a href="javaScript:void(0)" @click="cancel">< 取消</a>
    <a href="javaScript:void(0)">保存</a>
  </footer>
</div>
</template>
<script>

export default{
    data(){
        return{
            per:1,
            perMsg:"",
            msg:"",
            type_y:"免运费",
            list:[
                {
                value: '百分比',
                label: '百分比'
                },{
                value: '固定金额',
                label: '固定金额'
                },{
                value: '免运费',
                label: '免运费'
                }
                ],
            type_t:'1',
            order:[{
              value: '1',
              label: '适用所有订单'
            },{
              value: '2',
              label: '最低订单'
            },],
            disable:true,
            single:true,
            name:'',
          options: {
            disabledDate (date) {
              return date && date.valueOf() < Date.now() - 86400000;
            },
          },
          value: Date.now(),
        }
    },
  methods:{
    textF(){
      event.target.style.borderColor='skyblue';
    },
    textB(){
      if(this.name){
        this.msg='';
        event.target.style.borderColor='gainsboro';
      }else{
        this.msg='请使用字母或数字';
        event.target.style.borderColor='#e64751';
      }
    },
    input(){
      if(this.per){
        this.perMsg='';
        event.target.style.borderColor='gainsboro';
      }else{
        this.perMsg=this.type_y+'应该大于0';
        event.target.style.borderColor='#e64751';
      }
    },
    cancel(){
      this.$parent.show=true;
    },
    change(){
      if(this.single){
        this.single=true;
        this.disable=true
      }else{
        this.single=false;
        this.disable=false
      }
    }
  },
  mounted(){

  },
}
</script>
<style scoped>
  .set_card{
    background: white;
    margin-top: 0.2rem;
    box-shadow: 5px 5px 10px #E7E7E7;
    padding-left: 0.2rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;

  }
  h2{
    margin-bottom: 0.2rem;
    width: 1rem;
    text-align: center;
    color: #111111;
    font-size: 0.15rem;
    font-weight: 500;
    border-left:0.03rem solid #25B3EF;
    border-top-left-radius: 0.03rem;
    border-bottom-left-radius: 0.03rem;
  }
  h3{
    font-size: 0.14rem;
    margin-bottom: 0.05rem;
  }
  input{
    display: inline-block;  width:2rem;  border-radius:0.05rem;  border: 1px solid gainsboro;  padding: 0.065rem 0.1rem;
    margin-top: 3px;
  }
  .box{display: inline-block; height: 0.32rem;  width: 1.1rem;  line-height: 0.32rem;  padding: 0 0.1rem;  margin-left: -0.03rem; font-size: 0.14rem;margin-top: 0.01rem;background: none  !important; color: #111111;}
  footer{  position: fixed;  bottom: 0;  padding: 0.2rem 0.3rem;  margin-left: -0.2rem;  background: #fff;  box-shadow: 0 -2px 3px #ddd;  box-sizing: border-box;  width:calc(100vw - 7.7rem);  text-align: center;  }
  footer>a{  color: black;  display: inline-block;  padding: 0.1rem 0.5rem;  background: #eee;  border-radius: 0.05rem;  }
  footer>a:nth-child(1){  float: left;  }
  footer>a:nth-child(2){  color: white;  background: #93b719;  }
</style>



